<template>
  <div class="account-manage-container">
    <h1 class="page-title">Help & Support</h1>
    <div v-if="isLoading" class="loading">
      <img :src="require('@/assets/loading.gif')" alt="Loading" />
    </div>
    
    <template v-else>
      <div v-if="errorMessage" class="error-message">
        {{ errorMessage }}
      </div>
      <div class="info-box">
        <p class="warning">AI is not perfect, so please use your own judgement.</p>
        <h2>Currently Supported Specialties</h2>
        <ul class="specialties-list">
          <li>Master</li>
          <li>Cardiology</li>
          <li>Critical Care Medicine</li>
          <li>Endocrinology</li>
          <li>Extracorporeal Membrane Oxygenation</li>
          <li>Gastroenterology</li>
          <li>Hepatology</li>
          <li>Nephrology</li>
          <li>Pulmonology</li>
          <li>Rheumatology</li>
          <li>Allergy</li>
        </ul>
      </div>
      <div class="button-group">
        <button v-if="licenseNumber" @click="goToChatbot" class="backtochat-button">Back to Chatbot</button>
      </div>
    </template>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { auth } from '@/firebase';
import { onAuthStateChanged } from "firebase/auth";
import api from '@/utils/api';

export default {
  setup() {
    const router = useRouter();
    const email = ref('');
    const userName = ref('');
    const licenseNumber = ref('');
    const errorMessage = ref('');
    const isLoading = ref(true);

    onMounted(() => {
      onAuthStateChanged(auth, async (user) => {
        if (user) {
          email.value = user.email;
          userName.value = user.displayName || 'N/A';
          try {
            const response = await api.get('/api/check-license', { params: { email: user.email } });
            licenseNumber.value = response.data.license_number || '';
          } catch (error) {
            console.error('Error fetching license number:', error);
            errorMessage.value = 'Failed to fetch license information. Please try again later.';
          } finally {
            isLoading.value = false;
          }
        } else {
          router.push('/login');
        }
      });
    });

    const goToChatbot = () => {
      router.push('/chatbot');
    };

    return {
      email,
      userName,
      licenseNumber,
      errorMessage,
      isLoading,
      goToChatbot,
    };
  }
};
</script>

